import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router);

// 启用组件懒加载
export default new Router({
    //mode: 'history',
    routes: [
        {
            path: '/index',
            name: 'index',
            component:resolve => require(['../components/pages/index/index'], resolve)
        },
        {
            path: '/login',
            name: 'login',
            component: resolve => require(['../components/pages/login/login'], resolve)
        },
        {
            path: '/changePwd',
            name: 'change-pwd',
            component: resolve => require(['../components/pages/change-pwd/change-pwd'], resolve)
        },
        {
            path: '*',
            name: 'page-none',
            component: resolve => require(['../components/pages/page-none/page-none'], resolve)
        }
    ],
    scrollBehavior (to, from, savedPosition) {
        return {x: 0, y: 0}
    }
})



