// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import flexible from './components/lib/flexible'
import $ from 'jquery'
import easing from 'jquery.easing'

// 移动端调试器 注意在上线发布时关闭
import eruda from 'eruda'

import axios from 'axios';
import loginCheck from '@/components/util/loginCheck';

// axios 拦截器
axios.interceptors.request.use(
    config => {
        let token = sessionStorage.getItem('token');
        if (token != null) {
            config.params = {token};
        }
        return config
    }, function (error) {
        console.log((error));
        return Promise.reject(error)
    }
);

axios.interceptors.response.use(function (response) {
    if (response.data.errno == 101) {
        alert('请先登录');
        router.replace({
            name: 'login'
        });
    }
    return response
}, function (error) {
    console.log((error));
    return Promise.reject(error)
});

// 页面进入时的登录校验
router.beforeEach((to, from, next) => {
    if (to.name === 'login') {
        next()
    } else {
        loginCheck().then(next())
    }
});

flexible(window, window['lib'] || (window['lib'] = {}));

//eruda.init();

Vue.config.productionTip = false;

Vue.config.devtools = true;

/* eslint-disable no-new */
var vm = new Vue({
    el: '#app',
    data: {
        isLogin: false
    },
    router,
    template: '<App/>',
    components: {
        App
    }
});



