import axios from 'axios'

export default function ({pathname = 'admin/login/check', thirdParam = {}} = {}) {
    return new Promise((resolve, reject) => {
        axios.post(pathname)
            .then(response => {
                if (response.data.errno === 0) {
                    resolve(thirdParam);
                }
            })
            .catch(err => {
                reject();
                console.log(err);
            })
    });
}


